html {
    min-height: 100%;
}

body {
    background: linear-gradient(#081221, #03080f);
    font-family: 'Mulish', sans-serif;
    color: #c1d1e8;
}

.montserrat {
    font-family: 'Montserrat', sans-serif;
}

.white {
    color: #ffffff;
}

.page {
    width: 90vw;
    max-width: 1535px;
    margin: 0 auto;
}

@media only screen and (max-width: 991px) {
    .page {
        width: 96vw;
    }
}