.background {
  background-color: #0e1a2b;
  font-size: 13px;
  max-width: 330px;
  position: relative;
  margin-right: -4.5vw;
  margin-left: -1vw;
}

.input {
  width: 100%;
  background-color: #182c47;
  border: none;
  height: 30px;
  margin-top: -10px;
  color: #b6cae3;
}

.input::placeholder {
  color: #b6cae3;
}

.input:-ms-input-placeholder {
  color: #b6cae3;
}

.input::-ms-input-placeholder {
  color: #b6cae3;
}

.selectrow {
  width: 100%;
  position: relative;
}

.arrow {
  height: 30px;
  width: 30px;
  color: #ffffff;
  background-color: #5692e8;
  border: none;
  bottom: 16px;
  top: -5px;
  position: absolute;
  z-index: 5;
}

.arrow:focus {
  outline: none;
}

.select {
  width: 123%;
  color: #b6cae3;
  position: relative;
  z-index: 5;
}

.selected {
  background-color: #182c47;
  border: none;
  height: 30px;
  margin-top: -5px;
  color: #b6cae3;
  position: relative;
  cursor: pointer;
}

.options {
  width: 100%;
  background-color: #182c47;
  color: #b6cae3;
  margin-top: -16px;
  z-index: 5;
}

.option {
  cursor: pointer;
  width: 100%;
}

.clear {
  color: #ffffff;
  background-color: #5692e8;
  border: none;
  height: 25px;
  width: 65px;
  z-index: 4;
}

.clear:focus {
  outline: none;
}

@media only screen and (max-width: 1155px) {
  .arrow {
    bottom: -25px;
  }

  .select {
    margin-left: 30px;
}
} 

@media only screen and (max-width: 991px) {
  .background {
    width: 96vw;
    max-width: 96vw;
    margin: 0 auto;
  }

  .arrow {
    top: -5px;
  }

  .select {
      width: 117%;
      margin-left: -4%;
  }

  .options {
    position: absolute;
    opacity: .95;
  }

  .clear {
      margin-top: 4.5vh;
  }
}

@media only screen and (max-width: 835px) {
  .select {
    margin-left: -3%;
  }
}

@media only screen and (max-width: 661px) {
  .select {
    margin-left: 0;
  }
}

@media only screen and (max-width: 575px) {
  .select {
    width: 121%;
    margin-left: -3%;
  }

  .options {
    width: 90%;
    margin-left: 10%;
  }

  .clear {
    width: 95%;
    margin-left: 2%;
    margin-bottom: 20px;
    margin-top: 5px;
  }
}

@media only screen and (max-width: 460px) {
  .select {
    margin-left: 0;
  }
}